import { Field, InputType } from "@nestjs/graphql";
import { IsNotEmpty, IsNumber, IsString } from "class-validator";


@InputType()
export class PaypalBillingCyclePrice {

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  value: number;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  currency: string;
  
}
